import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import YouTube from "react-youtube";
import axios from "axios";
import Swal from "sweetalert2";
import Loader from "react-loader-spinner";

const ExerciseDetails = () => {
  const history = useHistory();
  const [exercise, setExercise] = useState([]);
  const [loading, setLoading] = useState(false);

  const API_KEY = "1f76ae49-7962-4113-94e8-1ba316485aa1";
  const URL = "https://tna-backend.herokuapp.com/api";
  const token = localStorage.getItem("user_token");
  const headers = {
    "api-key": API_KEY,
    Authorization: "bearer " + token
  };

  const getUserExercises = async () => {
    setLoading(true);
    try {
      const result = await axios.get(`${URL}/training/exercises/`, { headers });
      setLoading(false);
      result.data.map(exercise => {
        if (exercise.id == localStorage.getItem("exercise_id")) {
          console.log("Exercise", exercise);
          setExercise(exercise);
        }
      });
    } catch (e) {
      const error = e.response.data;
      const message = error[Object.keys(error)[0]];
      errorPopup(message);
      setLoading(false);
    }
  };

  const errorPopup = message => {
    Swal.fire({
      icon: "error",
      title: "Oops!...",
      text: message
    });
  };

  useEffect(() => {
    getUserExercises();
  }, []);

  const opts = {
    width: "100%"
  };

  const getVideo = () => {
    return exercise && exercise.video_url && exercise.video_url.split("v=")[1];
  };

  return (
    <>
      {loading ? (
        <Loader
          style={{ textAlign: "center" }}
          type="MutatingDots"
          color="white"
          height={100}
          width={100}
        />
      ) : (
        <div style={{ color: "white" }}>
          <div
            style={{
              display: "flex",
              justifyContent: "space-between",
              fontSize: "18px",
              alignItems: "center",
              marginBottom: "2rem"
            }}
          >
            <i
              onClick={() => history.push("/ejercicios")}
              className="fa fa-chevron-left"
              aria-hidden="true"
            ></i>
            <div
              style={{
                backgroundColor: "#c3ea4d",
                border: "2px solid #f8f9fa",
                padding: "3px 20px",
                borderRadius: "50px",
                color: "#212529"
              }}
            >
              {exercise && exercise.name}
            </div>
          </div>
          <div style={{ display: "flex", flexDirection: "column" }}>
            <YouTube videoId={getVideo()} opts={opts} />
            <div style={{ marginBottom: "5px" }}>
              <i
                className="fa fa-circle"
                style={{
                  fontSize: "8px",
                  color: "#c3ea4d",
                  marginRight: "5px"
                }}
                aria-hidden="true"
              ></i>
              {`Descripción: ${exercise && exercise.description}`}
            </div>
          </div>
        </div>
      )}
    </>
  );
};

export default ExerciseDetails;
