import React, { useEffect, useState } from "react";
import axios from "axios";
import Swal from "sweetalert2";
import workout from "../img/home/workout.jpg";
import { useHistory } from "react-router-dom";
import Loader from "react-loader-spinner";
import "../styles/session.scss";

const Sessions = props => {
  const API_KEY = "1f76ae49-7962-4113-94e8-1ba316485aa1";
  const URL = "https://tna-backend.herokuapp.com/api";
  const token = localStorage.getItem("user_token");
  const headers = {
    "api-key": API_KEY,
    Authorization: "bearer " + token
  };

  const history = useHistory();
  const [loading, setLoading] = useState(false);
  const [sessions, setSessions] = useState([]);

  const getUserRoutine = async () => {
    setLoading(true);
    try {
      const result = await axios.get(`${URL}/training/routines/`, { headers });
      setLoading(false);
      setSessions(result.data);
      console.log("sessions", result.data);
    } catch (e) {
      const error = e.response.data;
      const message = error[Object.keys(error)[0]];
      errorPopup(message);
      setLoading(false);
    }
  };

  const errorPopup = message => {
    Swal.fire({
      icon: "error",
      title: "Oops!...",
      text: message
    });
  };

  useEffect(() => {
    getUserRoutine();
  }, []);

  const onSessionClick = id => {
    localStorage.setItem("session_id", id);
    history.push("/ejercicios");
  };

  return (
    <div style={{ color: "white" }}>
      <div
        style={{
          display: "flex",
          justifyContent: "space-between",
          fontSize: "18px",
          alignItems: "center",
          marginBottom: "2rem"
        }}
      >
        <i
          onClick={() => history.push("/menu")}
          className="fa fa-chevron-left"
          aria-hidden="true"
        ></i>
        <div>Sesiones por día</div>
      </div>
      {loading ? (
        <Loader
          style={{ textAlign: "center" }}
          type="MutatingDots"
          color="white"
          height={100}
          width={100}
        />
      ) : (
        sessions &&
        sessions.map((session, i) => {
          return (
            <span
              key={session.id}
              className="session"
              style={{ backgroundImage: `url(${workout})` }}
              onClick={() => onSessionClick(session.id)}
            >
              <span style={{ padding: "0 0 8px 16px" }}>
                {`Sesión ${i + 1}`}
              </span>
            </span>
          );
        })
      )}
    </div>
  );
};

export default Sessions;
