import React, { useEffect, useState } from "react";
import RepsStepper from "./RepsStepper";
import { useHistory } from "react-router-dom";
import axios from "axios";
import Swal from "sweetalert2";
import Loader from "react-loader-spinner";

const Exercises = () => {
  const history = useHistory();

  const API_KEY = "1f76ae49-7962-4113-94e8-1ba316485aa1";
  const URL = "https://tna-backend.herokuapp.com/api";
  const token = localStorage.getItem("user_token");
  const headers = {
    "api-key": API_KEY,
    Authorization: "bearer " + token
  };

  const [loading, setLoading] = useState(false);
  const [exercises, setExercises] = useState([]);
  const [allExercises, setAllExercises] = useState([]);

  const getUserSessions = async () => {
    setLoading(true);
    try {
      const result = await axios.get(`${URL}/training/routines/`, { headers });
      setLoading(false);
      result.data.map(session => {
        if (session.id == localStorage.getItem("session_id")) {
          console.log("Routine_Exercises", session.routine_exercises);
          setExercises(session.routine_exercises);
        }
      });
    } catch (e) {
      const error = e.response.data;
      const message = error[Object.keys(error)[0]];
      errorPopup(message);
      setLoading(false);
    }
  };

  const getAllExercises = async () => {
    setLoading(true);
    try {
      const result = await axios.get(`${URL}/training/exercises/`, { headers });
      setLoading(false);
      setAllExercises(result.data);
    } catch (e) {
      const error = e.response.data;
      const message = error[Object.keys(error)[0]];
      errorPopup(message);
      setLoading(false);
    }
  };

  const errorPopup = message => {
    Swal.fire({
      icon: "error",
      title: "Oops!...",
      text: message
    });
  };

  useEffect(() => {
    getUserSessions();
    getAllExercises();
  }, []);

  const getReps = i => {
    return exercises[i].reps.split(",");
  };

  const getRest = i => {
    return exercises[i].rest.split("-");
  };

  const onHelpClick = exercise_id => {
    localStorage.setItem("exercise_id", exercise_id);
    history.push("/explicacion-ejercicio");
  };

  const getExerciseName = exercise_id => {
    var exerciseName = "";
    allExercises &&
      allExercises.map(exercise => {
        if (exercise.id === exercise_id) {
          exerciseName = exercise.name;
        }
      });
    return exerciseName;
  };

  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        color: "white"
      }}
    >
      <div
        style={{
          display: "flex",
          justifyContent: "space-between",
          color: "white",
          marginBottom: "24px",
          alignItems: "center",
          fontSize: "18px"
        }}
      >
        <i
          onClick={() => history.push("/sesiones")}
          className="fa fa-chevron-left"
          aria-hidden="true"
        ></i>
        <span>Sesión 1 - Ejercicios</span>
      </div>
      {loading ? (
        <Loader
          style={{ textAlign: "center" }}
          type="MutatingDots"
          color="white"
          height={100}
          width={100}
        />
      ) : (
        exercises &&
        exercises.map((exercise, i) => {
          return (
            <div
              key={i}
              style={{
                border: "2px solid white",
                borderRadius: "10px",
                padding: "15px 10px",
                marginBottom: "16px"
              }}
            >
              <div
                style={{
                  display: "flex",
                  justifyContent: "space-between",
                  alignItems: "center",
                  marginBottom: "1rem",
                  fontSize: "18px"
                }}
              >
                <span
                  style={{
                    backgroundColor: "#c3ea4d",
                    border: "2px solid #f8f9fa",
                    padding: "3px 20px",
                    borderRadius: "50px",
                    color: "#212529",
                    marginRight: "8px",
                    textOoverflow: "ellipsis",
                    overflow: "hidden",
                    whiteSpace: "nowrap"
                  }}
                >
                  {getExerciseName(exercise.exercise)}
                </span>
                <div
                  style={{
                    display: "flex",
                    flexDirection: "column",
                    alignItems: "center"
                  }}
                  onClick={() => onHelpClick(exercise.exercise)}
                >
                  <i className="fa fa-question-circle" aria-hidden="true"></i>
                  <span style={{ fontSize: "13px" }}>ayuda</span>
                </div>
              </div>
              <div style={{ display: "flex", flexDirection: "column" }}>
                <span style={{ marginBottom: "5px" }}>
                  <i
                    className="fa fa-circle"
                    style={{
                      fontSize: "8px",
                      color: "#c3ea4d",
                      marginRight: "5px"
                    }}
                    aria-hidden="true"
                  ></i>
                  {`Técnica: ${exercise.technique}`}
                </span>
                <span style={{ marginBottom: "5px" }}>
                  <i
                    className="fa fa-circle"
                    style={{
                      fontSize: "8px",
                      color: "#c3ea4d",
                      marginRight: "5px"
                    }}
                    aria-hidden="true"
                  ></i>
                  {`Repeticiones: ${getReps(i).length}`}
                </span>
                <span style={{ marginBottom: "5px" }}>
                  <i
                    className="fa fa-circle"
                    style={{
                      fontSize: "8px",
                      color: "#c3ea4d",
                      marginRight: "5px"
                    }}
                    aria-hidden="true"
                  ></i>
                  {`Notas: ${exercise.notes}`}
                </span>
              </div>
              <RepsStepper reps={getReps(i)} rest={getRest(i)} />
            </div>
          );
        })
      )}
    </div>
  );
};

export default Exercises;
